<template>
	<view>
		<view v-if="detail.serviceType === '迁出提档'" class="uni-center" style="background:#FFFFFF; font-size:0;">
			<image class="image" mode="widthFix" :src="detail.oldIdCardUrl" />
		</view>
		<uni-list v-if="detail.serviceType === '迁出提档'">
			<uni-list-item title="原车主姓名" :rightText="detail.oldCarOwner"/>
			<uni-list-item title="原车主身份证号码" :rightText="detail.oldCarDocumentNumber"/>
		</uni-list>
		
		<view v-if="detail.serviceType === '迁出提档'" class="uni-center" style="background:#FFFFFF; font-size:0;">
			<image class="image" mode="widthFix" :src="detail.newIdCardUrl" />
		</view>
		<uni-list v-if="detail.serviceType === '迁出提档'">
			<uni-list-item title="新车主姓名" :rightText="detail.newCarOwner"/>
			<uni-list-item title="新车主身份证号码" :rightText="detail.newCarDocumentNumber"/>
		</uni-list>
		
		<view class="uni-center" style="background:#FFFFFF; font-size:0;">
			<image class="image" mode="widthFix" :src="detail.vehicleLicenseUrl" />
		</view>
		<uni-list>
			<uni-list-item title="车牌号码" :rightText="detail.carId"/>
			<uni-list-item title="车架号" :rightText="detail.carNumber"/>
			<uni-list-item title="汽车品牌" :rightText="detail.carname"/>
			<uni-list-item title="汽车类型" :rightText="detail.carType"/>
		</uni-list>
		
		<uni-list>
			<uni-list-item title="业务类型" :rightText="detail.serviceType"/>
			<uni-list-item title="业务时间" :rightText="detail.bookingDate + detail.bookingTime"/>
			<uni-list-item v-if="detail.serviceType === '迁出提档'" title="迁入地" :rightText="detail.immigrationAddress"/>
			<uni-list-item v-if="detail.serviceType === '市内过户'" title="指标有效期" :rightText="detail.validDate"/>
			<uni-list-item title="状态" :rightText="detail.status"/>
			<uni-list-item title="失败原因" :rightText="detail.failReason"/>
			<uni-list-item title="备注" :rightText="detail.remark"/>
		</uni-list>
		<button class="button" type="warn" @click="deleteConfirm(detail.id)">删除</button>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations,
		mapActions
	} from 'vuex'
	
	export default {
		data () {
			return {
				detail: ''
			}
		},
		computed: {
			...mapState(['openid'])
		},
		onLoad(option) {
			this.detail = JSON.parse(option.data)
		},
		methods: {
			deleteConfirm(id) {
				const that = this
				uni.showModal({
				    content: '确定要删除吗？',
				    success: function (res) {
				        if (res.confirm) {
				            that.del(id)
				        } else if (res.cancel) {
				        }
				    }
				});
			},
			del(id) {
				this.$request({
					url: '/api/v1/booking/' + id,
					method: 'DELETE'
				}).then((res) => {
					if (res.resultCode === 200) {
						uni.showToast({
							title: '删除成功'
						})
						
						uni.$emit('bookingDelete')
						
						setTimeout(() => {
							uni.navigateBack()
						}, 1500)
					} else {
						uni.showToast({title: res.msg, icon:"none"})
					}
				})
			}
		}
	}
</script>

<style>
	.image{
		width: 90%;
		margin: 10rpx auto;
	}
	
	.button {
		width: 80%;
		margin: 20rpx auto;
	}
</style>
